.cell {
  height: 20px;
  width: 20px;
  border: 1px solid skyblue;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.weight {
  /* background-color: red; */
  content: url(https://s3.amazonaws.com/iconbros/icons/icon_pngs/000/001/586/original/weight.png?1555055014);
  background-size: 20px 20px;
}

.weight-animated {
  /* background-color: red; */
  content: url(https://s3.amazonaws.com/iconbros/icons/icon_pngs/000/001/586/original/weight.png?1555055014);
  background-size: 20px 20px;
  animation: weighted 0.2s linear;
}

.wall {
  background-color: black;
  border: 1px solid black;
}

.wall-animated {
  background-color: black;
  border: 1px solid black;
  animation: wall 0.2s linear;
}

.unvisited,
.unvisited-animated {
  background-color: aliceblue;
}

.visited {
  background-color: rgb(153, 153, 238);
  border-radius: 0%;
}

.visited-animated {
  animation: visit-animation 0.5s linear;
  background-color: rgb(153, 153, 238);
  border-radius: 0%;
}

.path {
  background-color: yellow;
  border: 1px solid yellow;
}

.path-animated {
  background-color: yellow;
  border: 1px solid yellow;
  animation: shortestPathAnimation 0.2s linear;
}

.source {
  content: url(https://cdn.icon-icons.com/icons2/1936/PNG/32/placeholder1_122378.png);
  background-size: 19px 19px;
  background-color: yellow;
  border: 1px solid yellow;
}

.destination {
  content: url(https://image0.flaticon.com/icons/png/32/985/985802.png);
  background-size: 20px 20px;
  background-color: yellow;
  border: 1px solid yellow;
}

@keyframes weighted {
  0% {
    transform: scale(1.8);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes wall {
  0% {
    transform: scale(1.8);
    background-size: 30%;
    background-color: grey;
    border-color: grey;
  }
  100% {
    transform: scale(1);
    background-size: 100%;
    background-color: black;
    border-color: black;
  }
}

@keyframes visit-animation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgb(153, 153, 238);
  }

  75% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
    background-color: rgb(153, 153, 238);
    border-radius: 0%;
  }
}

@keyframes shortestPathAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgb(248, 248, 186);
    border-radius: 100%;
  }

  100% {
    transform: scale(1);
    background-color: yellow;
    border: 1px solid yellow;
    border-radius: 0%;
    /* border-radius: 8px; */
  }
}

.grid {
  /* background-color: darkgrey; */
  /* padding: 20px; */
  border: 20px solid black;
  padding: 0;
}

.row {
  display: flex;
}

.flex-box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
#controlPanel {
  display: flex;
  justify-content: center;
  align-items: center;
}

#controlPanel button,
input,
label {
  margin: 10px 5px 10px 5px;
}

#controlPanel button {
  background-color: skyblue;
  border: 1px solid blue;
  border-radius: 5px 5px;
  height: 40px;
}

#controlPanel button:hover {
  background-color: rgb(64, 181, 228);
}

.verticalLine {
  border-left: 3px solid blueviolet;
  border-radius: 4px 4px;
  height: 30px;
  margin: 10px;
}
